
#wrpl-app{
  background: #eef1f5 !important;
  height: 100vh;
}
.wrpl-sidebar, .wrpl-admin-main, .wrpl-admin-main > .el-row, .wrpl-sidebar > .el-menu{
  height: 100%;
}
.el-table__body, #wrpl-app{
  font-family: 'Poppins' !important;
}
.el-table__body .cell{
  font-size: 11px;
}
#wpfooter{
  display: none;
}
#wpbody-content{
  padding-bottom: 0 !important;
}
.wrpl-sidebar{
  background: white;
}
.es_sidebar_heading{
  margin-left: 16px;
  margin-top: 16px;
}
.wrpl-sidebar hr{
  margin: 0 !important;
}
.wrpl-sidebar span{
  font-size: 13px;
}
.wrpl-sidebar .el-menu{
  border: 0;
}


